.metadata-container {
    text-align: center;
    padding: 0 20px 20px 20px;
    font-family: "Courgette", cursive;
}

.app-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    border: 1px solid #000;
    background-color: #f0f0f0;
    margin: 0 -20px 30px -20px;
}

.app-logo {
    display: flex;
    align-items: center;
}

.app-search-icon {
    width: 35px;
    height: 35px;
    color: #e63946;
}

.app-title {
    font-size: 40px;
    color: #e63946;
    font-weight: bold;
    margin-left: 5px;
}

.metadata-title {
    font-size: 48px;
    color: #e63946;
    margin-top: 20px;
    margin-bottom: 10px;
    font-weight: bold;
    text-align: center;
}

.metadata-card {
    width: 50%;
    margin: 0 auto;
    background: #f2f2f2;
    border: 2px solid #ccc;
    border-radius: 12px;
    padding: 25px;
    display: flex;
    gap: 30px;
    justify-content: center;
    align-items: center;
}

.metadata-image {
    width: 250px;
    height: 250px;
    object-fit: cover;
    border: 2px solid #ccc;
    border-radius: 10px;
}

.metadata-info {
    text-align: left;
    font-size: 20px;
    line-height: 1.6;
}

.divider {
    width: 90%;
    border: none;
    border-top: 3px solid #e63946;
    margin: 10px auto 20px;
}

.label {
    font-weight: bold;
    color: black;
    font-size: 20px;
}

.metadata-links {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.metadata-links a {
    color: rgb(0, 110, 255);
    font-size: 18px;
    font-family: serif;
}

.metadata-links a:hover {
    text-decoration: underline;
}

.app-links {
    margin-top: 40px;
    display: flex;
    justify-content: center;
    gap: 20px;
}

.app-links a {
    color: rgb(0, 110, 255);
    font-size: 18px;
    font-family: serif;
}

.app-links a:hover {
    text-decoration: underline;
}