.register-container {
    text-align: center;
    padding: 20px;
    font-family: "Courgette", cursive;
}

.register-title {
    font-size: 48px;
    color: #e63946;
    margin-bottom: 20px;
}

.register-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
}

.register-form input {
    width: 260px;
    padding: 10px;
    border: 2px solid #ccc;
    border-radius: 8px;
    font-size: 18px;
}

.register-button {
    background: #e63946;
    color: white;
    border: none;
    padding: 12px 24px;
    font-size: 20px;
    border-radius: 10px;
    cursor: pointer;
}

.register-error {
    color: red;
    font-size: 18px;
}

.register-success {
    color: green;
    font-size: 18px;
}

.register-text {
    margin-top: 20px;
    font-size: 18px;
    font-family: serif;
}

.register-text a {
    color: rgb(0, 110, 255);
    font-size: 18px;
}

.register-text a:hover {
    text-decoration: underline;
}