.home-container {
    text-align: center;
    padding: 0 20px 20px 20px;
    font-family: "Courgette", cursive;
}

.app-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    border: 1px solid #000;
    background-color: #f0f0f0;
    margin: 0 -20px 30px -20px;
}

.app-logo {
    display: flex;
    align-items: center;
}

.app-search-icon {
    width: 35px;
    height: 35px;
    color: #e63946;
}

.app-title {
    font-size: 40px;
    color: #e63946;
    font-weight: bold;
    margin-left: 5px;
}

.home-title {
    font-size: 48px;
    color: #e63946;
    margin-top: 20px;
    margin-bottom: 10px;
    font-weight: bold;
    text-align: center;
}

.search-section {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.search-box {
    display: flex;
    align-items: center;
    border: 3px solid #e63946;
    padding: 10px;
    border-radius: 10px;
    background: #eee;
    width: 350px;
}

.search-box input {
    border: none;
    background: transparent;
    width: 100%;
    font-size: 18px;
    outline: none;
}

.search-icon {
    font-size: 28px;
    margin-right: 10px;
    color: #e63946;
}

.search-button {
    background: #e63946;
    color: white;
    border: none;
    padding: 12px 24px;
    font-size: 20px;
    border-radius: 10px;
    cursor: pointer;
}

.recent-title {
    font-size: 36px;
    margin-top: 40px;
    color: #e63946;
}

.divider {
    width: 90%;
    border: none;
    border-top: 3px solid #e63946;
    margin: 10px auto 20px;
}

.login-warning {
    color: red;
    font-size: 20px;
}

.recent-list {
    list-style: none;
    padding: 0;
}

.recent-item {
    background: #f2f2f2;
    border: 2px solid #ccc;
    padding: 10px 20px;
    margin: 8px 0;
    font-size: 18px;
    border-radius: 8px;
    cursor: pointer;
}

.app-links {
    margin-top: 40px;
    display: flex;
    justify-content: center;
    gap: 20px;
}

.app-links a {
    color: rgb(0, 110, 255);
    font-size: 18px;
    font-family: serif;
}

.app-links a:hover {
    text-decoration: underline;
}