.results-container {
    text-align: center;
    padding: 0 20px 20px 20px;
    font-family: "Courgette", cursive;
}

.app-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    border: 1px solid #000;
    background-color: #f0f0f0;
    margin: 0 -20px 30px -20px;
}

.app-logo {
    display: flex;
    align-items: center;
}

.app-search-icon {
    width: 35px;
    height: 35px;
    color: #e63946;
}

.app-title {
    font-size: 40px;
    color: #e63946;
    font-weight: bold;
    margin-left: 5px;
}

.results-title {
    font-size: 48px;
    color: #e63946;
    margin-top: 20px;
    margin-bottom: 10px;
    font-weight: bold;
    text-align: center;
    font-family: "Courgette", cursive;
}

.results-list {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.results-card {
    background: #ccc;
    border: 2px solid black;
    border-radius: 10px;
    padding: 15px;
    margin-bottom: 20px;
    cursor: pointer;
    transition: background 0.2s, box-shadow 0.2s;
    width: 60%;
    align-items: center;
}

.results-card:hover {
    background: #eaeaea;
    box-shadow: 0px 3px 6px rgba(0,0,0,0.15);
}

.results-row {
    display: grid;
    grid-template-columns: 150px 1fr 1fr;
    align-items: center;
    width: 100%;
}

.results-image {
    width: 120px;
    height: 120px;
    object-fit: cover;
    border-right: 2px solid #ccc;
    padding-right: 10px;
}

.results-column {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 10px;
}

.divider {
    width: 90%;
    border: none;
    border-top: 3px solid #e63946;
    margin: 10px auto 20px;
}

.label {
    font-size: 20px;
    color: black;
}

.value {
    margin-top: 5px;
    font-size: 20px;
}

.return-home {
    margin-top: 25px;
    text-align: center;
}

.return-home a {
    color: rgb(0, 110, 255);
    font-size: 18px;
    font-family: serif;
}

.return-home a:hover {
    text-decoration: underline;
}

.app-links {
    margin-top: 40px;
    display: flex;
    justify-content: center;
    gap: 20px;
}

.app-links a {
    color: rgb(0, 110, 255);
    font-size: 18px;
    font-family: serif;
}

.app-links a:hover {
    text-decoration: underline;
}